            let hm= timeArr[0].split(':');
            let h = hm[0];
            let m = hm[1];
            let mode = timeArr[1]

                let t00 = m.indexOf('00');
                let t15 = m.indexOf('15');
                let t30 = m.indexOf('30');
                let t45 = m.indexOf('45');
